<template>
  <v-row justify="center" align="center" class="MParagraphImage">
    <v-col cols="12" md="6" :order="(!invert || $vuetify.breakpoint.smAndDown) ? 'first' : 'last'">
      <v-img :src="require('@/assets/images/' + img)" class="mx-auto" :width="imageWidth"></v-img>
    </v-col>
    <v-col cols="12" md="6">
      <p>
        Molto spesso viene detto che la derivata misura un
        <em>tasso di cambiamento istantaneo</em>.
        <br />Fermati un attimo, e rifletti su questa frase. Noti che c'è qualcosa che non torna?
        <br />Questa frase è un po' paradossale:
        una cambiamento è qualcosa che accade fra due punti nel tempo, mentre "istantaneo" è qualcosa
        che accade in un momento specifico nel tempo.
      </p>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "MParagraphImage",
  props: {
    img: {
      type: String,
      required: true
    },
    invert: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    imageWidth() {
      if (this.$vuetify.breakpoint.smOnly) return "80%";
      else return '100%';
    }
  }
};
</script>

<style lang="scss" scoped>
</style>