<template>
  <MArticle :title="title">
    <MQuote>
      Finché le leggi della matematica si riferiscono alla realtà, non sono certe, e finché sono certe, non si riferiscono alla realtà
      <i>- Albert Einstein</i>
    </MQuote>

    <MParagraphTitle>Il "Tasso di cambiamento istantaneo"</MParagraphTitle>

    <MParagraphImage img="oxymoron.png">
      Molto spesso viene detto che la derivata misura un
      <em>tasso di cambiamento istantaneo</em>.
      <br />Fermati un attimo, e rifletti su questa frase. Noti che c'è qualcosa che non torna?
      <br />Questa frase è un po' paradossale:
      una cambiamento è qualcosa che accade fra due punti nel tempo, mentre "istantaneo" è qualcosa
      che accade in un momento specifico nel tempo.
    </MParagraphImage>

    <br />
    <br />

    <p>Immagina una macchina, che parte da un punto A ed arriva in un punto B:</p>

    <v-img src="@/assets/images/car.gif" max-height="350px"></v-img>

    <p>
      <br />Possiamo tracciarne il grafico mettendo la distanza sull'asse delle y e il tempo su quella delle x:
    </p>

    <v-img src="@/assets/images/carGraph.png"></v-img>

    <p>
      <br />Inialmente la curva è molto bassa, visto che la macchina è lenta a partire.
      Piano piano che la macchina accellera possiamo vedere che la distanza percorsa in un secondo diventa sempre più grande, fino a che, verso la fine, la macchina rallenta
      e la curva si appiatisce.
      La velocità, quindi, in questo grafico, è definita dall'inclinazione della curva.
    </p>

    <v-img src="@/assets/images/graphSlope.png"></v-img>

    <!-- <br />
    <v-row>
      <v-col cols="4">
        <p>Possiamo anche tracciare direttamente il grafico della velocità:</p>
      </v-col>
      <v-col cols="8">
        <v-img src="@/assets/images/speedGraph.png"></v-img>
      </v-col>
    </v-row>-->

    <!----------------------->
    <!------ Velocity ------->
    <!----------------------->
    <MParagraphTitle>Ma che cos'è la velocità?</MParagraphTitle>

    <p>
      Fermiamoci un attimo a pensare al concetto di velocità. Intuitivamente tutti sapremmo rispondere alla domanda "quanto stiamo andando veloci in questo momento?"
      Sempre facendo riferimento all'esempio dell automobile, basterebbe gurdare il tachimetro.
    </p>

    <p>
      Il problema è che la velocità in un "momento" non ha senso.
      Immagina l'animazione ad inizio pagina, messa in pausa in un momento a caso. Sapresti dire quanto sta andando veloce la macchina?
    </p>

    <v-img src="@/assets/images/frameCar0.png" />

    <br />

    <p>Credo proprio che la risposta sia no. Questo perchè la velocità, proprio da definizione, è il rapporto fra due variazioni:</p>

    <p id="velocityFormula" class="math text-center">{{velocityFormula}}</p>

    <p>Quindi per calcolarla, ci serviranno due punti distinti nello spazio e nel tempo, per esempio:</p>

    <v-img src="@/assets/images/frameCar1.png" />

    <!-- ------------------------------------------ -->

    <p>
      <br />Visto che ci servono
      <b>due</b> momenti distinti,
      come facciamo a calcolare la velocità in un
      <b>istante</b> di tempo?
    </p>

    <p>
      Partiamo rappresentando questo il concetto di "variazione" sul grafico, indicando i due termini come
      <i>ds</i> e
      <i>dt</i>
      (variazione per la distanza e per il tempo). Di conseguenza possiamo immaginare il calcolo della velocità come
      $$ds \over dt$$ (che, come accennato all'inizio, equivale a calcolare l'inclinazione della curva):
      <v-img src="@/assets/images/dsDt.png"></v-img>
    </p>

    <p>
      Visto che la curva è rappresentata da una funzione
      possiamo ricavarci $$v(t)$$ basata solo su $$dt$$:
    </p>

    <p id="velocityFormulaGraph" class="math text-center">{{velocityFormulaGraph}}</p>

    <p>
      $$s(t+dt) - s(t)$$ equivale al "pezzetino" blu dell'immagine, ovvero
      $$ds$$. $$s(t)$$ è
      il valore della funzione (s) per un valore t (nel caso dell'immagine, 3)
      mentre $$s(t+dt)$$ è un secondo valore preso dopo un intervallo di tempo
      $$dt$$.
      Per ottenere la velocità quindi ci basterà dividere questo valore per la differenza di tempo
      <i>dt</i> (ovvero il "pezzettino" giallo dell'immagine).
    </p>

    <p>
      Ritornando un attimo all'esempio precendente, il tachimetro per calcolare la velocità fa esattmente questo:
      prende un valore molto piccolo di $$dt$$ ad esempio 1 secondo, 100 millisecondi, ecc.
      e periodicamente misura la distanza che la macchina ha percorso.
      Quindi per essere precisi, il tachimetro non misurerà mai la velocità in un istante, ma solamente in un intervallo di tempo
      <b>molto</b> piccolo.
    </p>

    <p class="relevant">
      La derivata alla fine non è altro che questo: al posto di prendere un valore specifico
      e molto piccolo di $$dt$$ (appunto come 1s, 100ms, ...), si pone $$dt$$
      <i>tendente</i>
      a 0:
      $$\lim_{dt \to 0} {s(t + dt) - s(t) \over dt}$$
    </p>

    <v-img src="@/assets/images/tangent.gif"></v-img>

    <p>
      <br />Possiamo notare anche un'altra cosa interessante: tendendo a 0 i due punti del grafico si andranno a sovrapporre e quindi
      il rapporto $$ds \over dt$$ (che equivale al coefficente angolare della retta fra i due punti)
      sarà uguale al coefficente angolare della retta tangente in punto del grafico!
    </p>

    <p>
      Quindi al posto di pensare una derivata come un tasso di variazione istantaneo,
      sarebbe meglio pensarla come la migliore approssimazione possibile del tasso di variazione
      <i>intorno</i> ad un punto
      (dove questo intorno sarà infinitesimamente piccolo).
    </p>

    <p class="relevant">
      PS: Prima di concludere vorrei fare un appunto sulla terminologia: nella matematica qualsiasi cosa scritta come $$dy \over dx$$
      (ovvero una qualsiasi variazione di y su una qualsiasi variazione di x) è considerata derivata, ed è quindi implicito che i valori
      tenderanno a 0.
    </p>

    <p>
      Concludendo, vorrei ricollegarmi alla citazione di Einstein d'inizio pagina. La derivata, come spero tu abbia capito,
      è un concetto puramente matematico, proprio perchè nella realtà il concetto di limite è impensabile: nel mondo reale
      avremo sempre bisogno di un valore di $$dt$$, anche se infinitesimamente piccolo, dovrà comunque essere un valore definito. 
      Nonostante le derivate siano un concetto puramente teorico sono un'ottimo strumento per descrivere la realtà (anche se non esattamente!).
    </p>
  </MArticle>
</template>

<script>
import MArticle from "@/components/MArticle.vue";
import MQuote from "@/components/MQuote.vue";
import MParagraphTitle from "@/components/MParagraphTitle.vue";
import MParagraphImage from "@/components/MParagraphImage.vue";
import MathLive from "mathlive";

export default {
  name: "IntegralArticle1",
  components: {
    MArticle,
    MQuote,
    MParagraphTitle,
    MParagraphImage
  },
  props: ["id", "title"],
  data() {
    return {
      velocityFormula:
        "$$v(t) = {Variazione\\ del\\ tempo \\over Variazione\\ della\\ distanza}$$",
      velocityFormulaGraph: "$$v(t) = {s(t + dt) - s(t) \\over dt}$$"
    };
  },
  created() {
    this.$nextTick(function() {
      MathLive.renderMathInDocument();
    });
  }
};
</script>

